.login-container {
  width: 100%;
  max-width: 900px;
  border: 1px solid #e1e2e3;
  border-radius: 0.2rem;
  box-shadow: 0 0 0 #000;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}

.login-form-1 {
  padding-top: 70px;
}

.login-form-1 h5 {
  color: #356aae;
}

.login-form-1 .btnSubmit {
  font-weight: 600;
  color: #fff;
  background-color: #356aae;
  width: 100%;
  border-radius: 0rem;
  padding: 1.5%;
  border: none;
  margin-top: 7px;
  cursor: pointer;
}

.login-form-2 {
  background: #356aae;
  color: #fff;
}
.login-form-2 h3 {
  color: #fff;
  padding-top: 70px;
}
.login-form-2 h6 {
  color: #fff;
  padding-top: 45px;
}
.login-form-2 p {
  padding-top: 40px;
}
.owl-theme .owl-nav {
  display: none;
}
.owl-theme .owl-dots .owl-dot {
  margin-top: 15px;
  margin-left: -10px;
}
.owl-carousel .owl-item img {
  display: initial;
  width: 90%;
}
button.owl-dot.active {
  background: white !important;
}

button.owl-dot {
  height: 10px !important;
  font-size: 13px !important;
  background: #d6d6d6 !important;
  color: black !important;
  margin: 5px !important;
  width: 10px;
  border-radius: 50%;
}

.errormsg {
  font-size: 13px;
  color: red;
  margin: 0 !important;
}
