/* ========Navbar css======= */
.mainNav {
  transition: 0.1s;
  /* border: 1px solid green;  */
  position: sticky;
  top: 0;
  z-index: 500;
  margin-left: 250px;
}
.pushMainNav {
  margin-left: 0px !important;
}

.topnav {
  color: white;
  display: flex;
  justify-content: space-between;
  padding: 7px 0px 21px 5px;
  width: 100%;
  background-color: var(--main-color);
}

.topnav .nav-left {
  /* border: 1px solid red; */
  display: flex;
}

.topnav .nav-right {
  /* border: 1px solid red; */
  display: flex;
  justify-content: flex-end;
}

.nav-user-details {
  /* border: 1px solid green; */
  display: flex;
  padding: 0 10px 0 15px;
}

.nav-user-profile {
  width: 48px;
  height: 48px;
  border-radius: 25px;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.nav-user-info {
  display: flex;
  flex-direction: column;
  padding: 0 0 0 15px;
}

.nav-icons,
.nav-search-icons,
.nav-power-icons {
  margin-top: 15px;
  margin-right: 25px;
  cursor: pointer;
}

.nav-menu-icons {
  margin-top: 15px;
  cursor: pointer;
}

.MuiPaper-root.MuiPopover-paper.MuiPaper-elevation8.MuiPaper-rounded {
  box-shadow: none;
}

.sec-logout {
  border: 2px solid #e1e2e3;
  display: flex;
  padding: 10px 40px;
}

.btn-logout {
  font-family: "Open Sans", sans-serif !important;
  font-size: 13px !important;
  border: none;
  background-color: white;
  font-weight: 650;
  color: #727399;
}

@media screen and (max-width: 795px) {
  .nav-icons {
    display: none;
  }
  .mainNav {
    transition: 0.1s;
    margin-left: 0px;
  }
  .pushMain {
    margin-left: 0px !important;
  }
}
